import { useEffect } from "react";
import { register } from "@teamhanko/hanko-elements";
import { useNavigate } from "react-router-dom";

const hankoApi = "https://ebfb53cd-dd15-4937-acab-90326fefdb9b.hanko.io";

export default function HankoProfile() {
  const navigate = useNavigate();
  useEffect(() => {
    register(hankoApi).catch((error) => {
      // handle error
      console.log(error);
    });
  }, []);

  return (
    <div
      style={{
        display: "grid",
        justifyContent: "center",
        alignItems: "center",
        minHeight: "60vh",
        marginTop:"200px"
      }}
    >
      <div>
        <hanko-profile style={{ borderRadius: "10px" }} />
      </div>

      <div
        onClick={() => navigate("/")}
        style={{
          padding: "15px 30px",
          backgroundColor: "#4CAF50",
          color: "white",
          border: "none",
          borderRadius: "5px",
          cursor: "pointer",
          fontSize: "24px",
        }}
      >
        Back to Home Page
      </div>
    </div>
  );
}
