// import Home from "./Home";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import VideoPage from "./components/Page/Episodes";
import Animelist from "./components/Animelist/Animelist";
import Header from "./components/header/Header";
import Trailer from "./components/Trailer/Trailer";
import HankoAuth from "./components/hankoAuth/HankoAuth";
import Home from "./Home";
import HankoProfile from "./components/hankoAuth/HankoProfile";
import Logout from "./components/hankoAuth/Logout";


function App() {
  // const ReqAuth=()={
  //   if()
  // }
  return (
    <BrowserRouter basename="/anime-site">
      <Routes>
        <Route path="/" element={<Home />} />
      </Routes>
      <Routes>
        <Route path="/login" element={<HankoAuth />} />
        <Route path="/profile" element={<HankoProfile />} />
        <Route path="/logout" element={<Logout />} />
      
      
        <Route
          path="/episode"
          element={
            <>
              <Header />
              <VideoPage />
            </>
          }
        />
        <Route
          path="/anime"
          element={
            <>
              <Header />
              <Animelist />
            </>
          }
        />
        <Route
          path="/trailer"
          element={
            <>
              <Header />
              <Trailer />
            </>
          }
        />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
